#about {
	background-color: #fff;
	color: #333;
	padding: 60px 20px;
	transition: background-color 0.3s, transform 0.3s;
  }
  
  #about h2 {
	font-size: 2.5em;
  }
  
  #about a {
	display: inline-block;
	margin-top: 20px;
	padding: 10px 20px;
	border: 2px solid #ff6347;
	border-radius: 5px;
	transition: background-color 0.3s, color 0.3s;
  }
  
  #about a:hover {
	background-color: #ff6347;
	color: white;
  }
  