.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 100s linear;
  }
  .name {
    animation: fadein 3s linear;
  }
}

.App-header {
  background-color: #010149;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    color: rgb(170, 118, 14);
  }
  to {
    opacity: 1;
    color: rgb(255, 255, 255);
  }
  
 }

 body {
  font-family: 'Arial, sans-serif';
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
}

.App {
  text-align: center;
}

nav {
  background-color: #333;
  padding: 10px;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  transition: color 0.3s;
}

nav ul li a:hover {
  color: #ff6347;
}

section {
  padding: 60px 20px;
  transition: background-color 0.3s, transform 0.3s;
}

section:hover {
  background-color: #eaeaea;
  transform: scale(1.01);
}

h1, h2 {
  color: #333;
}

a {
  color: #ff6347;
  text-decoration: none;
  transition: color 0.3s;
}

a:hover {
  color: #ff4500;
}

footer {
  background-color: #333;
  color: white;
  padding: 20px;
}

footer a {
  color: white;
  margin: 0 10px;
}
